@use "global" as *;

.tab {
    width: 100%;
  }
  
  .tab__list {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    column-gap: rem(21);
    row-gap: rem(16);
    justify-items: center;

    @include mq("md") {
      display: flex;
      column-gap: rem(0);
      row-gap: rem(10);
    }
  }

    .tab__list.tab__list--page-information {
      display: grid;
      grid-template-columns: repeat(3,1fr);
      column-gap: rem(20);
      row-gap: rem(16);
      justify-items: center;
  
      @include mq("md") {
        column-gap: rem(90);
      }

  }
  
  .tab__button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: rem(34);
    width: 100%;
    padding: rem(6) rem(0);
    flex-shrink: 0;
    cursor: pointer;
    border: 1px solid $green2;

    color: $green2;
    font-size: rem(14);
    font-style: normal;
    font-weight: $regular;
    line-height: 1.57143; /* 157.143% */
    background-color: $white;
    transition: all .5s;

    @include mq("md") {
      width: rem(162);
    }

  }

  .tab__button.tab__button--page-information {
    // display: inherit;
    // justify-content: inherit;
    // align-items: inherit;
    // height: inherit;
    // line-height: inherit; /* 157.143% */

    // width: 100%;
    // padding: rem(6) rem(0);
    // flex-shrink: 0;
    cursor: pointer;

    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    height: inherit;
    line-height: 1.57143; /* 157.143% */
    max-width: rem(300);
    font-size: rem(14);
    padding: rem(10) rem(15) rem(10) rem(15);
    text-align: right;

    border: none;
    text-align: center;

    color: $green2;
    // font-size: rem(14);
    font-style: normal;
    font-weight: $regular;
    background-color: $green3;

    position: relative;

    box-shadow: rem(2) rem(2) rem(4) rem(0) rgba(0, 0, 0, 0.25);

    @include mq("md") {
      display: inherit;
      justify-content: inherit;
      align-items: inherit;
      height: inherit;
      line-height: 1.57143; /* 157.143% */
      width: 100%;
      max-width: rem(300);
      font-size: rem(24);
      padding: rem(29) rem(30) rem(26) rem(35);
      text-align: right;
      box-shadow: none;
    }

  }
  .tab__button.tab__button--page-information.tab__button--page-information-fun {
    padding: rem(10) rem(15) rem(10) rem(15);

      @include mq("md") {
        padding: rem(29) rem(18) rem(26) rem(35);
      }

  }

  .tab__button:hover {
    background-color: $green2;
    color: $white;
  }

  .tab__button.tab__button--page-information-license,
  .tab__button.tab__button--page-information-fun,
  .tab__button.tab__button--page-information-experience{
    position: relative;
  }

