/* mask-image
=====================================================*/
// 
  .tab__button.tab__button--page-information-license::before,
  .tab__button.tab__button--page-information-fun::before,
  .tab__button.tab__button--page-information-experience::before {
  @include mq("md") {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: calc(50% - (min(46.062vw, rem(121))));
    transform: translateY(-50%);
    width: rem(64);
    height: rem(64);
    mask-size: contain;
    mask-repeat: no-repeat;
    background-color: $green2; // 
    z-index: 2;
    transition: all .5s;
  }
}

// 
.tab__button.tab__button--page-information-license::before {
  @include mq("md") {
    mask-image: url(../images/common/icon-information1-green.svg);
  }
}
.tab__button.tab__button--page-information-license:hover::before,
.tab__button.tab__button--page-information-license.is-active::before {
  @include mq("md") {
    background-color: $white; // 
  }
}

// 
.tab__button.tab__button--page-information-fun::before {
  @include mq("md") {
    mask-image: url(../images/common/icon-information2.svg);
    left: calc(50% - (min(46.062vw, rem(132))));

  }
}
.tab__button.tab__button--page-information-fun:hover::before,
.tab__button.tab__button--page-information-fun.is-active::before {
  @include mq("md") {
    background-color: $white; // 
    left: calc(50% - (min(46.062vw, rem(132))));
  }
}

// 
.tab__button.tab__button--page-information-experience::before {
  @include mq("md") {
    mask-image: url(../images/common/icon-information3.svg);
  }
}
.tab__button.tab__button--page-information-experience:hover::before,
.tab__button.tab__button--page-information-experience.is-active::before {
  @include mq("md") {
    background-color: $white; // 
  }
}

  .tab__button:not(:first-child) {

    @include mq("md") {
      margin-left: rem(32);
    }
  }

  .tab__button.tab__button--page-information:not(:first-child) {

    @include mq("md") {
      margin-left: rem(0);
    }
  }

  .tab__button.is-active {
    color: #fff;
    background-color: $green2;
  }

  .tab__contents {
    margin-top: rem(-8);
