@use "global" as *;

.modal-open-button {
    font-size: rem(16);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: rem(20);
    cursor: pointer;
    color: #fff;
    background: #000;
  }
  
  .modal {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    display: flex;
    visibility: hidden;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: rem(40) rem(20);
    transition: opacity 0.3s, visibility 0.3s;
    opacity: 0;
    background: rgba(0, 0, 0, 50%);
  }
  
  .modal.is-open {
    visibility: visible;
    opacity: 1;
  }
  
  .modal__body {
    position: relative;
    display: inline-block;
    width: 90%;
  }
  
  .modal__close-button {
    position: absolute;
    top: rem(10);
    right: rem(10);
    font-size: rem(12);
    display: flex;
    align-items: center;
    justify-content: center;
    width: rem(60);
    padding: rem(10);
    cursor: pointer;
    color: #000;
    border: 1px solid #000;
  }
  
  .modal__content {
    max-height: 80vh; // 80%
    // overflow: auto; // 
    padding-top: rem(70);
  }
  
  .modal__text {
    font-size: rem(16);
    margin-top: rem(20);
  }

  .modal__content img {
    max-height: 100%; // 100%
    max-width: 100%; // 100%
    height: auto; // 
    width: auto; // 
    object-fit: contain; // 
    display: block; // 
    margin: auto; // 
  }
  

  
  .modal__content img {
    max-height: 75vh; // 75%
    max-width: 100%; // 100%
    object-fit: contain; // 
    display: block; // 
    margin: 0 auto; // 
  }
  